import { Overlay, OverlayPositionBuilder, OverlayRef } from '@angular/cdk/overlay';
import { ComponentPortal, ComponentType, TemplatePortal } from '@angular/cdk/portal';
import { Directive, ElementRef, HostListener, Input, OnInit, TemplateRef, ViewContainerRef } from '@angular/core';

@Directive({
  selector: '[customTooltip]',
  standalone: true,
})
export class CustomTooltipDirective implements OnInit {
    @Input('tooltipId') uniqueId: any;
    @Input('tooltipAdditionalparameter') additionalParameter: any;
    @Input('customTooltip') tooltipContent: TemplateRef < any > | ComponentType < any > ;
    private _overlayRef: OverlayRef;
    private isTooltipOnclick;

    constructor(
        private overlay: Overlay,
        private overlayPositionBuilder: OverlayPositionBuilder,
        private elementRef: ElementRef,
        private viewContainerRef: ViewContainerRef,
    ) {}

    ngOnInit(): void {
        this.isTooltipOnclick = this.additionalParameter['tooltipOnclick'] === "true" ? true : false;
        if (this.tooltipContent) {
            let customClass = (this.uniqueId) ? "custom-tooltip-" + this.uniqueId : "custom-tooltip"
            const position = this.overlayPositionBuilder
                .flexibleConnectedTo(this.elementRef)
                .withPositions([{
                        originX: 'center',
                        originY: 'bottom',
                        overlayX: 'center',
                        overlayY: 'top',
                        offsetX: 10,
                        offsetY: 10
                    },
                    {
                        originX: 'center',
                        originY: 'top',
                        overlayX: 'center',
                        overlayY: 'bottom',
                        offsetX: 0,
                        offsetY: -10
                    }
                ]);

            this._overlayRef = this.overlay.create({
                positionStrategy: position,
                scrollStrategy: this.overlay.scrollStrategies.close(),
                panelClass: customClass
            });
        } else {
            // console.error('Error, occured in html custom tooltip!');
        }
    }

    private showTooltip() {
        //attach the template/component if it has not already attached to the overlay
        if (this._overlayRef && !this._overlayRef.hasAttached()) {
            let containerPortal: TemplatePortal < any > | ComponentPortal < any > ;
            if (this.tooltipContent instanceof TemplateRef) {
                containerPortal = new TemplatePortal(this.tooltipContent, this.viewContainerRef);
            } else {
                containerPortal = new ComponentPortal(this.tooltipContent, this.viewContainerRef);
            }
            this._overlayRef.attach(containerPortal);
        }
    }

    @HostListener('click')
    toggle() {
        if (this.isTooltipOnclick) {
            this.showTooltip();
        }
    }

    @HostListener('mouseover')
    show() {
        if (!this.isTooltipOnclick) {
            this.showTooltip();
        }
    }

    @HostListener('mouseleave')
    hide() {
        if (!this.isTooltipOnclick) {
            this.closeToolTip();
        }
    }

    @HostListener('focus', ['$event.target'])
    onFocus(target: any) {
        this.showTooltip();
    }

    @HostListener('focusout', ['$event.target'])
    onFocusout(target: any) {
        this.closeToolTip();
    }

    ngOnDestroy() {
        this.closeToolTip();
    }

    private closeToolTip() {
        if (this._overlayRef) {
            this._overlayRef.detach();
        }
    }
}